<template>
  <div class="outBox">
    <div class="swiperBox">
        <!-- swiper -->
      <van-swipe :autoplay="3000">
        <van-swipe-item 
        v-for="(item, index) in swiperData" 
        :key="index"
        >
          <img v-lazy="item.bannerImg" />
        </van-swipe-item>
        
      </van-swipe>
    </div>

    <div class="contentBox">
      <!-- sidebar区域 -->
      <div class="leftBox">
        <div class="left_sidebar">
          <div class="product_type"
          :class="{sidebar_demo:num==index}"
          v-for="(item,index) in ProductType"
          :key="index"
          @click="clickProductType(item,index)"
          >{{item.typeDesc}}</div>
        </div>
        <!-- line -->
        <div class="line" ref="line" :style="{top:top+'px'}"></div>
      </div>
        <!-- 数据区域 -->
        <div class="right_product ">
          <div class="productsBox" 
          v-for="(item,index) in ProductData_ByType"
          :key="index"
          @click="productsDetail(item.pid)"
          >
            <div class="imgBox">
              <img v-lazy="item.smallImg">
            </div>
            <p class="cnName outText">{{item.name}}</p>
            <p class="enName outText">{{item.enname}}</p>
            <p class="price">￥{{item.price}}</p>
          </div>
        </div>
    </div>

    <tabbar></tabbar>
  </div>
</template>

<script>
import tabbar from '../../components/tabbar'
import "../../assets/less/menu.less";
//导入辅助函数
import { createNamespacedHelpers } from "vuex";
//创建基于menuModule命名空间下的辅助函数
const { mapState: ms , mapMutations:mm } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      num:0,//记录当前点击的商品类型下标
      swiperData: [], //swiper图片
      ProductType:null,//产品类型
      ProductData_ByType:[],//产品类型对应数据
      top:0,//移动线条top
      line:null,//线条元素
      currentDataObj:null,//预存当前点击的数据，切换路由时保证数据正确

    };
  },
  components:{
    tabbar
  },
  created() {
    this.num = this.current//
    this.top = this.tops//
    this.currentDataObj = this.currentDataObjs//
    
    
    //获取swiper图片数据
    this.getSwiperData();
    //获取商品类型
    this.getProductType()

    //获取商品数据
    // this.getProductData_ByType({type:'isHot'})
    this.getProductData_ByType(this.current==0?{type:'isHot'}:this.currentDataObj)

    //判断
    if(!localStorage.getItem('__Tk')){
      localStorage.setItem('isloginCbg','')
    }
  },

  methods: {
      ...mm(['changeData','changeProducts','changeCurrent']),
    //获取swiper图片数据
    getSwiperData() {
      //判断menuModule下state中有没有数据，如果存在缓存数据就不发起请求
      if (this.swiperDataCache.length > 0) {
        this.swiperData = this.swiperDataCache;
        
        return;
      }

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "./banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 300) {
            this.swiperData = result.data.result;
            

            this.changeData({
              key: "swiperDataCache",
              value: result.data.result
            });
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    },
    
    //点击商品类型
    clickProductType(item,index){
      this.num=index//储存当前下标
      this.currentDataObj = item//储存当前数据
      //调用商品数据
      this.getProductData_ByType(this.currentDataObj)
    },

    //获取商品类型 
    getProductType(){

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method:"GET",
        url:'/type',
        params:{
          appkey:this.appkey
        }
      }).then(result => {
        this.$toast.clear();
        
        if(result.data.code == 400){
          result.data.result.unshift({type: 'isHot', typeDesc: '最新推荐'});   
          //
          this.ProductType = result.data.result
          
          
          
                
        }
      }).catch(() => {
        this.$toast.clear();
        
        
      })
    },

    //根据商品类型获取商品数据typeProducts
    getProductData_ByType(item){
      //在缓存获取商品数据
      if (this.ProductData_ByTypeCache[item.type]) {
        this.ProductData_ByType = this.ProductData_ByTypeCache[item.type];
        
        return;
      }

      this.ProductData_ByType = [];

       let params = {
        appkey: this.appkey,
        key: 'type',
        value: item.type
      };

      if(item.type=='isHot'){
        params.key = 'isHot';
        params.value = 1;
      }
      
      
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      this.axios({
        method:"GET",
        url:'/typeProducts',
        params
      }).then(result => {
        this.$toast.clear();
        
      
       this.ProductData_ByType = result.data.result

          //缓存商品数据
          this.changeProducts({
            type: item.type,
            value: result.data.result
          })
          
       
      }).catch(() => {
        this.$toast.clear();
        
        
      })
    },

    //跳转详情页  查看商品详情
    productsDetail(pid){

    this.$router.push({name:'detail',query:{pid}})
    }
  },
  mounted(){
    //渲染后拿到line元素
    this.line = this.$refs.line
    
  },
  watch:{
    //监听商品类型当前下标
    num(){
      
      this.top = this.num*this.line.offsetHeight
    }
  },
  computed: {
    ...ms(["swiperDataCache",'ProductData_ByTypeCache','current','tops','currentDataObjs'])
  },
  beforeDestroy(){
    this.$store.commit('menuModule/changeCurrent',{current:this.num,top:this.top,currentDataObj:this.currentDataObj}) 
    
    
  }
};
</script>

<style lang="less" scoped>
</style>